/* hero css start */
.hero {
  padding-top: 300px;
  padding-bottom: 200px;
  position: relative;
  z-index: 1;
  @media (max-width: 1199px) {
    padding-top: 200px;
    padding-bottom: 120px; }
  @media (max-width: 380px) {
    padding-top: 165px;
    padding-bottom: 90px; }
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.45;
    z-index: -1; }
  &__title {
    font-size: 36px;
    margin-bottom: 35px;
    @media (max-width: 767px) {
      font-size: 32px; }
    @media (max-width: 575px) {
      font-size: 28px; } }
  &__info-text {
    font-size: 14px;
    margin-top: 20px;
    color: rgba(#ffffff, 0.8);
    i {
      font-size: 16px; } } }
.hero__search {
  position: relative;
  @include d-flex;
  background-color: #ffffff;
  padding: 10px;
  @include border-radius(8px);
  z-index: 1;
  &::before {
    position: absolute;
    content: '';
    top: -10px;
    left: -10px;
    width: calc(100% + 20px);
    height: calc(100% + 20px);
    background-color: rgba(#ffffff, 0.15);
    @include border-radius(8px);
    z-index: -1; }
  .form-control {
    width: calc(100% - 130px);
    background-color: transparent;
    border: none;
    @include border-radius(0); }
  .search-btn {
    min-width: 130px;
    text-align: center;
    background-color: transparent;
    border: none;
    color: $para-color;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $base-color;
    color: #ffffff;
    @include border-radius(5px);
    @include transition(all 0.3s);
    @media (max-width: 380px) {
      min-width: 100%;
      padding: 10px 20px; }
    &:hover {
      background-color: darken($base-color, 5%); }
    i {
      font-size: 20px;
      margin-right: 10px; } } }
/* hero css end */


/* inner hero css start */
.inner-hero {
  padding-top: 215px;
  padding-bottom: 120px;
  position: relative;
  &::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.45; } }
/* inner hero css end */
