/* about section css start */
.about-item {
  @include d-flex;
  &__icon {
    font-size: 52px;
    color: $base-color;
    width: 52px;
    line-height: 1; }
  &__content {
    width: calc(100% - 52px);
    padding-left: 20px;
    .title {
      margin-bottom: 15px; } } }
.about-thumb {
  height: 100%;
  position: relative;
  box-shadow: 0 5px 15px rgba(#000000, 0.15);
  img {
    width: 100%;
    height: 100%;
    @include object-fit; }
  .content {
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 30px 20px;
    overflow: hidden;
    @media (max-width: 480px) {
      padding: 20px 15px; }
    &::before {
      position: absolute;
      content: '';
      top: -22px;
      left: -10px;
      width: 30px;
      height: 66px;
      background-color: #ffffff;
      @include transform(rotate(45deg)); }
    .year {
      font-size: 52px;
      font-weight: 700;
      @media (max-width: 480px) {
        font-size: 36px; }
      @media (max-width: 380px) {
        font-size: 32px; } }
    .caption {
      font-size: 18px;
      @media (max-width: 480px) {
        font-size: 16px; }
      @media (max-width: 380px) {
        font-size: 14px; } } } }
/* about section css end */


/* counter section css start */
.counter-item {
  @include d-flex;
  align-items: center;
  @media (max-width: 480px) {
    justify-content: center;
    text-align: center; }
  &__icon {
    width: 52px;
    font-size: 48px;
    line-height: 1; }
  &__content {
    width: calc(100% - 52px);
    padding-left: 15px;
    padding-bottom: 5px;
    position: relative;
    @media (max-width: 480px) {
      width: 100%;
      padding-left: 0;
      margin-top: 10px; }
    &::after {
      position: absolute;
      content: '';
      bottom: 0;
      left: 15px;
      width: 35px;
      height: 1px;
      background-color: $base-color;
      @media (max-width: 480px) {
        left: 50%;
        margin-left: -18px; } }
    .title {
      font-size: 36px;
      @media (max-width: 480px) {
        font-size: 32px; } }
    p {
      @media (max-width: 480px) {
        font-size: 14px; } } } }
/* counter section css end */


/* journalist section css start */
.journalist-card {
  padding: 15px;
  @include border-radius(5px);
  box-shadow: 0 0 15px rgba(#000000, 0.05);
  @include transition(all 0.3s);
  background-color: #ffffff;
  @include d-flex;
  &:hover {
    box-shadow: 0 0 25px rgba(#000000, 0.15);
    @include transform(translateY(-5px)); }
  &__thumb {
    width: 40%;
    @media (max-width: 767px) and (min-width: 576px) {
      width: 100%; }
    @media (max-width: 380px) {
      width: 100%; }
    img {
      width: 100%;
      @include border-radius(5px); } }
  &__content {
    width: 60%;
    padding: 10px 10px 10px 20px;
    background-color: #ffffff;
    @media (max-width: 767px) and (min-width: 576px) {
      text-align: center;
      width: 100%;
      padding: 20px 10px 10px 10px; }
    @media (max-width: 380px) {
      text-align: center;
      width: 100%;
      padding: 20px 10px 10px 10px; } }
  &__meta {
    margin-top: 10px;
    li {
      font-size: 14px;
      i {
        font-size: 16px; } } } }

.journalist-filter-form {
  background-color: #ffffff;
  @include d-flex;
  .form-control {
    width: calc(100% - 55px);
    background-color: transparent;
    @include border-radius(5px 0 0 5px);
    border-right: none; }
  .journalist-search-btn {
    width: 55px;
    background-color: $base-color;
    color: #ffffff;
    @include border-radius(0 5px 5px 0); } }
/* journalist section css end */


/* choose us section css start */
.choose-card {
  padding: 30px;
  @include border-radius(8px);
  @include d-flex;
  border: 1px solid rgba(#ffffff, 0.35);
  @include transition(all 0.3s);
  &:hover {
    background-color: $base-color;
    border-color: $base-color;
    @include transform(translateY(-5px)); }
  &__icon {
    width: 52px;
    font-size: 52px;
    line-height: 1; }
  &__content {
    width: calc(100% - 52px);
    padding-left: 20px;
    @media (max-width: 1199px) and (min-width: 992px) {
      width: 100%;
      padding-left: 0;
      margin-top: 20px; }
    @media (max-width: 420px) {
      width: 100%;
      padding-left: 0;
      margin-top: 20px; } } }
/* choose us section css end */


/* how work section css start */
.work-card {
  text-align: center;
  &__icon {
    position: relative;
