/* how work section css end */


/* cta section css start */
.cta-wrapper {
  background-color: #ffffff;
  @include d-flex;
  @include border-radius(8px);
  overflow: hidden;
  box-shadow: 0 5px 25px rgba(#000000, 0.1);
