/* testimonial section css end */


/* client section css start */
.client-item {
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  img {
    display: inline-block;
    height: 70px; } }
.client-slider {
  .slick-arrow {
    position: absolute;
    top: 0;
    height: 100%;
    width: 40px;
    background-color: #f1f1f1;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    cursor: pointer;
    font-size: 24px;
    &:hover {
      background-color: $base-color;
      color: #ffffff; }
    &.prev {
      left: -40px;
      @media (max-width: 1500px) {
        left: 0; } }
    &.next {
      right: -40px;
      @media (max-width: 1500px) {
        right: 0; } } } }
