      left: -50px;
      width: 200px;
      height: 200px;
      border: 20px solid rgba(#ffffff, 0.25);
      @include border-radius(50%); }
    &::before {
      position: absolute;
      content: '';
      bottom: -50px;
      right: -50px;
      width: 200px;
      height: 200px;
      border: 15px solid rgba(#ffffff, 0.15);
      @include border-radius(50%); } }
  .right {
    width: 50%;
