/* chat section css start */
.bubble-left {
  float: left;
  clear: both;
  background: #ffffff;
  -webkit-box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.14);
  box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.14);

  &:before {
    content: " ";
    display: block;
    position: relative;
    top: 0px;
    left: -11px;
    height: 13px;
    width: 13px;
    background: inherit;

    /*#B7CC90 */
    z-index: 100;
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg); } }

.bubble-right {
  float: right;
  clear: both;
  text-align: right;
  color: #fff;
  background-color: #2196f3;
  -webkit-box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.14);
  box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.14);

  &:before {
    content: " ";
    display: block;
    position: relative;
    top: 0px;
    right: -99.8%;
    height: 13px;
    width: 13px;
    background: inherit;

    /*#90B2CC */
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg); } }

.message-container {
  display: block;
  height: 620px;
  background: #ffffff;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.12), 0 1px 5px 0 rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  overflow: hidden;

  .message-north {
    width: 100%;
    height: 100%;

    .message-user-list {
      list-style-type: none;
      float: left;
      clear: left;
      width: 25%;
      height: 100%;
      overflow-x: hidden;
      overflow-y: scroll;
      @media (max-width: 991px) {
        display: none; }
      a {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
        padding: 10px;
        text-decoration: none;
        color: #313131;

        &:hover {
          border-right: 3px solid #212121;
          background: #eceff1;
          -webkit-transition: all ease .2s;
          -moz-transition: all ease .2s;
          -ms-transition: all ease .2s;
          -o-transition: all ease .2s;
          transition: all ease .2s; }

        .user-img {
          height: 40px;
          width: 40px;
          min-width: 40px;
          min-height: 40px;
          background: #6A8BBA;
          overflow: hidden;
          border-radius: 50%;
          -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          -o-border-radius: 50%; }

        .user-details {
          -ms-flex: 0 0 calc(100% - 40px);
          flex: 0 0 calc(100% - 40px);
          max-width: calc(100% - 40px);
          padding-left: 15px; }

        .user-desc {
          padding-top: 2px;
          font-size: 12px;
          color: #5A5A5A;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          margin: 0; }

        &.active {
          border-right: 3px solid #212121;
          background: #eceff1; }

        &.highlight {
          background: #90B2CC;

          .user-title, .user-desc {
            font-weight: bold; } } } }

    .message-right {
      float: right;
      width: 75%;
      height: 100%;
      @media (max-width: 991px) {
        width: 100%; }
      .message-thread {
        height: 80%;
        padding-left: 10px;
        padding-right: 10px;
        background: #F5F5F5;
        overflow-x: hidden;
        overflow-y: scroll; } }

    .message-thread {
      .message {
        min-width: 40%;
        max-width: 70%;
        margin: 5px;
        margin-bottom: 2%;
        padding: 3px 5px 3px 5px;
        cursor: pointer;

        > p {
          display: block;
          clear: both;
          margin-left: 3px;
          margin-right: 3px;
          font-size: 15px; }

        label {
          margin-top: -13px;
          font-size: 14px;
          font-weight: bold;
          color: #515151;
          cursor: pointer; } }

      .bubble-right label {
        color: #f1f1f1; }

      .message {
        .message-user {
          display: block;
          float: left;
          margin-left: 3px; }

        .message-timestamp {
          display: block;
          float: right;
          margin-right: 3px;
          text-align: right;
          font-weight: 400; } } } }

  .message-south {
    padding: 37px 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    input {
      width: calc(100% - 100px);
      height: 40px;
      padding: 10px 20px;
      outline: none;
      resize: none;
      font-size: 13px;
      color: #666;
      background: #eceff1;
      border: none;
      border-radius: 5px;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      -ms-border-radius: 5px;
      -o-border-radius: 5px;
      @media (max-width: 575px) {
        width: 100%; } }

    button {
      display: inline-block;
      margin-top: 0px;
      height: 35px;
      width: 80px;
      border: none;
      cursor: pointer;
      background-color: #2196F3;
      color: #ffffff; } } }

.message-user-list,
.message-thread {
  scrollbar-width: thin;
  scrollbar-color: #90A4AE#CFD8DC;
  &::-webkit-scrollbar {
    width: 11px; }
  &::-webkit-scrollbar-track {
    background: #CFD8DC; }
  &::-webkit-scrollbar-thumb {
    background-color: #90A4AE;
    border-radius: 6px;
    border: 3px solid #CFD8DC; } }
/* chat section css end */

/* stories section css start */
.story-search-wrapper {}

.story-card {
  padding: 15px;
  background-color: #ffffff;
  @include border-radius(5px);
  box-shadow: 0 5px 10px rgba(#000000, 0.1);
  &__thumb {
    height: 220px;
    overflow: hidden;
    @include border-radius(5px);
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #e0e0e0;
    img {
      width: 100%;
      height: 100%;
      @include object-fit; } }
  &__meta {
    margin-bottom: 20px;
    .author {
      @include d-flex;
      align-items: center;
      .thumb {
        width: 35px;
        height: 35px;
        @include border-radius(50%);
        overflow: hidden;
        img {
          width: inherit;
          height: inherit;
          @include object-fit; } }
      .name {
        width: calc(100% - 35px);
        font-size: 14px;
        padding-left: 15px; } } }
  &__content {
    padding: 20px 10px 10px 10px;
    p {
      font-size: 14px; } }
  &__title {
    font-size: 16px; }
  audio {
    width: 100% !important; } }
/* stories section css end */

/* story details section css start */
.story-details {
  padding: 50px;
  background-color: #ffffff;
  border: 1px solid #e5e5e5;
  box-shadow: 0 5px 5px rgba(#000000, 0.05);
  @media (max-width: 767px) {
    padding: 30px; }
  &__thumb {
    margin-bottom: 35px;
    img {
      @include border-radius(10px); } }
  &__header {
    .left {
      width: calc(100% - 250px);
      @media (max-width: 767px) {
        width: 100%; }
      .thumb {
        width: 65px;
        height: 65px;
        overflow: hidden;
        @include border-radius(50%); }
      .name {
        width: calc(100% - 65px);
        padding-left: 15px; } }
    .right {
      width: 250px; } }
  &__content {
    p {
      margin-top: 20px; } } }
/* story details section css end */
